import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Layout } from 'antd'
import ErrorBoundary from '@/components/error-boundary'
import Exception from '@/components/exception'
import Loading from '@/components/loading'
import AppHeader from '@/components/app-header'
import { useRoutes, RouteItem } from '@/constants/routes'
import Home from '@/containers/home'

function App() {
  const routes = useRoutes()

  const renderRouteItem = React.useCallback((item: RouteItem) => {
    const path = `${item.path}${item.param ? `/${item.param}` : ''}`

    return (
      <Route
        exact
        key={item.id}
        path={path}
        component={
          item.component ? item.component : () => <h1> {item.title} </h1>
        }
      />
    )
  }, [])

  const renderRoute: any = React.useCallback(
    (data: RouteItem[]) => {
      return data.map((item: RouteItem) => {
        return [
          renderRouteItem(item),
          item.children && renderRoute(item.children),
          item.hide && renderRoute(item.hide),
        ]
      })
    },
    [renderRouteItem]
  )

  return (
    <ErrorBoundary>
      <Layout>
        <AppHeader />
        <React.Suspense fallback={<Loading />}>
          <Switch>
            <Route exact path="/" component={Home} />
            {renderRoute(routes)}
            <Route component={Exception} />
          </Switch>
        </React.Suspense>
      </Layout>
    </ErrorBoundary>
  )
}

export default App
